.teams {
    width: 100%;
    padding: 32px;
    box-sizing: border-box;
    text-align: center;
    font-size: 32px;
    position: relative;
}
.input__color {
    position: absolute;
    top: 20px;
    right: 50px;
    border-radius: 5px;
}
.teams h3 {
    font-weight: 400;
    color: #212121;
    border-bottom: 5px solid;
    display: inline-block;
    padding-bottom: 8px;
}
.collaborators {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}